html,body {
    font-size: 12.5px;
    font-family: 'Noto Sans TC', sans-serif;
    background:#000 url(../images/bg.jpg) top center no-repeat fixed ;
    background-size: cover;
    @include media-breakpoint-up(md) {
      font-size: 14px;
    }
    @include media-breakpoint-up(xl) {
      font-size: 16px;
    }
}
.page {
  display: flex;
  flex-direction: column;
  height:100vh;
  justify-content: space-between;
}
@include media-breakpoint-down(sm) { 
  .col-4 {
    padding-right: 0.5rem;
    padding-left: 0.5rem;
  }
}
@mixin bg-cover {
  background-size: cover;
  background-position: center center;
}
$golden:linear-gradient(0deg, #c2a224 0, #fdeda3 25%, #e1c96b 50%, #fdeda3 75%, #e1c96b 100%);
.radius-big {
  border-radius: 100px;
}
.max-w {
  max-width: 800px;
  // min-height: 730px;
  margin-right: auto;
  margin-left: auto;
}
.header {
  background: linear-gradient(0deg, #0E0E0E 10%, #363636 100%);
  box-shadow: 0px 0px 10px #929292;
}
.navbar {
  padding: 0;
  justify-content: center;
  &-expand-md {
    flex-wrap: wrap;
  }
  &-brand {
    img {
      height: 30px;
      max-width: 8rem;
    }
  }
  &-collapse {
    flex-grow: 0;
  }
  &-nav {
    width: 100%;
    flex-direction: row;
  }
  @include media-breakpoint-up(sm) {
    &-brand {
      img {
        height: auto;
      }
    }
  }
  @include media-breakpoint-up(lg) {
    &-nav {
      margin-left: 0;
    }
  }

  &-sm {
    width: 100%;
    background: linear-gradient(0deg, #0E0E0E 10%, #363636 100%);
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 100;
    @include media-breakpoint-up(md) {
      display: none;
    }
  }
}
.nav {
  &-item {
    padding-left: 1px;
    flex: 1 1 auto;
  }
  &-link {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 1.25rem;
    transition: color .3s;
    i {
      font-size: 1.5rem;
      line-height: 1.5rem;
    }
    &:hover, &.active {
      color: $secondary;
      text-shadow: 1px 1px 0 rgba($light, .6);
      background: $golden;
    }
  }
  @include media-breakpoint-up(lg) {
    &-item {
      flex-grow: auto;
    }
  }
}
.navbar-expand-md .navbar-nav .nav-link {
  padding: 1rem 1.25rem;
}
.sign {
  display: flex;
  align-items: center;
  margin-left: auto;
  .dropdown-toggle {
    margin-left: .5rem;
    &::after {
      display: none;
    }
    @include media-breakpoint-up(md) {
      border-left: 1px solid rgba($primary, 0.3);
    }
  }
  .dropdown-menu {
    padding: 0.5em;
    border: 1px solid rgba($primary, 0.3);
    background: linear-gradient(0deg, #0E0E0E 0, #363636 100%);
    border-radius: 10px;
  }
  .dropdown-item {
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    text-align: center;
    color: $primary;
    &:hover {
      background-color: rgba($primary, 0.3);
      border-radius: 5px;
    }
  }
  a {
    &:hover {
      text-decoration: none;
    }
  }
  &-data {
    border-top: 1px solid rgba($primary, 0.2);
    width: 100%;
    justify-content: center;
  }
  @include media-breakpoint-up(xl) {
    &-data {
      width: auto;
      border-top: none;
    }
  }
}
.avatar {
  display: flex;
  align-items: center;
  margin: .5rem;
  >* {
    margin-left: 0.5rem;
  }
  > i {
    width: 2.5rem;
    height: 2.5rem;
    position: relative;
    background-image: none;
    img {
      border-radius: 100px;
      width: auto;
      max-width: 100%;
    }
    &::before {
      position: absolute;
      content: '';
      width: 1.5rem;
      height: 1.5rem;
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      top: 0;
      right: 0;
      transform: translateX(50%) translateY(-25%);
      border-radius: 100px;
    }
    &.icon-v {
      &::before {
        background-image: url(../images/i-v.png);
      }
    }
    &.icon-vv {
      &::before {
        background-image: url(../images/i-vv.png);
      }
    }
    &.icon-mv {
      &::before {
        background-image: url(../images/i-mv.png);
      }
    }
  }
  .name {
    max-width: 5rem;
    overflow: hidden;
    white-space: nowrap;
    position: relative;
    &::after {
      position: absolute;
      content: '';
      width: 30%;
      height: 100%;
      right: 0;
      background: linear-gradient(90deg, rgba($black, 0) 0%, rgba(14, 14, 14, .7) 100%) ;
    }
  }
}
.user-data {
  color: $primary;
  i {
    color: $yellow;
    margin-right: 0.5em;
    margin-left: 0.5em;
    font-size: 1.25rem;
  }
  &:last-child {
    margin-left: 1rem;
    border-left: 1px solid rgba($primary, 0.3);
  }
}

.marquee {
  padding-left: 4em;
  margin-top: 1em;
  width: 100%;
  height: 2.75em;
  line-height: 2.75em;
  overflow: hidden;
  background-color: $light;
  color: $secondary;
  border-radius: 200px;
  position: relative;
  &::after {
    position: absolute;
    display: block;
    content: "\f106";
    font-family: Flaticon;
    font-size: 1em;
    color: $secondary;
    left: 0 ;
    top: 0;
    padding-left: 1.5em;
    padding-right: 1em;
    background-color: $light;
  }
  span {
   &:nth-child(n+1) {
     margin-left: 2em;
   }
  }
}
.ad {
  margin-top: 1rem;
  overflow-y: hidden;
  flex-wrap: nowrap;
  &-item {
    border: 1px solid rgba($primary, .5);
    border-radius: .75rem;
    overflow: hidden;
    position: relative;
    img {
      width: auto;
      max-width: 100%;
      height: 110px;
      transition: transform .3s;
    }
    &:hover {
      .ad-btn {
        transform: translateY(0%);
        opacity: 1;
      }
      img {
        transform: scale(1.1);
      }
    }
    @include media-breakpoint-up(sm) {
      img {
        height: auto;
      }
    }
  }
  &-btn {
    position: absolute;
    bottom: 0;
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 0.5em;
    transition: all .3s ease;
    transform: translateY(25%);
    opacity: 0;
    a {
      border-radius: .25em;
      padding: .25em;
      width: 50%;
      margin-left: 0.5em;
      transform: filter .3s;
      &:first-child {
        margin-left: 0;
      }
    }
  }
  
}
.btn {
  &-gold {
    color: $secondary;
    background: linear-gradient(0deg, #e1c96b 0%, #fdeda3 15%, #e1c96b 90%, #fdeda3 100%);
    border: 1px solid #fff29b;
    &:hover {
      color: $secondary;
      filter: brightness(1.1);
    } 
  }
  &-gray {
    background: linear-gradient(0deg, #494949 0%, #747474 15%, #494949 90%, #747474 100%);
    color: $light;
    border: 1px solid rgba($primary, .7);
  }

}
.tit {
  width: 100%;
  height: 2.5rem;
  margin-top: 0.25rem;
  margin-bottom: 0.25rem;
  &-game1{
    background: url(../images/tit-1.png) center center no-repeat;
    background-size: auto 60%;
  }
  &-game2{
    background: url(../images/tit-2.png) center center no-repeat;
    background-size: auto 60%;
  }
  &-news {
    margin-top: 1.5rem;
    font-size: 1.5rem;
    color: rgba($yellow, 0.85);
    padding-top: .5rem;
    padding-bottom: .5rem;

  }
  @include media-breakpoint-up(sm) {
    height: 3.5rem;
  }
}
.game{
  &:nth-child(n+4) {
    margin-top: 1rem;
  }
  &-multi {
    overflow: hidden;
    position: relative;
    p {
      background-color: rgba($black, .5);
      width: 100%;
      text-align: center;
      color: rgba($light, 0.85);
      font-size: 1.1rem;
      line-height: 2rem;      
      text-shadow:1px 2px 2px rgba($black, .75) ;
      z-index: 10;
      margin-bottom: 0;
    }
    img {
      width: 100%;
      max-width: 100%;
      transition: transform .3s;
      &:hover {
        transform: scale(1.1);
      }
    }
    @include media-breakpoint-up(sm) {
      p {
        position: absolute;
        top: 0;
      }
    }
    @include media-breakpoint-up(md) {
      p {
        font-size: 1.25rem;
        line-height: 2.5rem; 
      }
    }
  }
  &-single {
    padding: .5em;
    background-color: rgba($black, .35);
    position: relative;
    >a {
      overflow: hidden;
      display: block;
      position: relative; 
      padding: 1px;
      &::after {
        position: absolute;
        content: '';
        width: 100%;
        top: 0;
        bottom: 0;
        left: 0;
        background:$golden;
        z-index: 0;
      }
    }
    img {
      display: block;
      overflow: hidden;
      max-width: 100%;
      transition: transform .3s;
      position: relative;
      z-index: 1;
      &:hover {
        transform: scale(.95);
      }
    }
    p {
      color: $primary;
      font-size: 1.1rem;
      text-align: center;
      margin-top: 0.25rem;
      padding-bottom: 0.25rem;
      border-bottom: 1px solid rgba($primary, 0.3);
      overflow: hidden;
      white-space: nowrap;
      position: relative;
      &::after {
        position: absolute;
        content: '';
        width: 20%;
        height: 100%;
        right: 0;
        background: linear-gradient(90deg, rgba($black, 0) 0%, rgba(14, 14, 14, 1) 100%) ;
      }
    }
    @include media-breakpoint-up(md) {
      padding: 1em;
      a {
        padding: 2px;
      }
    }
  }
  &-nav {
    display: flex;
    justify-content: center;
    margin-top: .5em;
    a {
      width: 50%;
      max-width: 170px;
      padding-top: .25em;
      padding-bottom: .25em;
      &:nth-child(2) {
        margin-left: .5rem;
      }
    }
    @include media-breakpoint-up(md) {
      a {
        font-size: 1.1rem;
        padding-top: .5em;
        padding-bottom: .5em;
      }
    }
  }
  .game-nav {
    flex-wrap: wrap;
    a {
      width: auto;
      &:nth-child(2) {
        margin-left: 0;
        margin-top: 0.5rem;
      }
    }
    @include media-breakpoint-up(sm) {
      flex-wrap: nowrap;
      a {
        width: 50%;
        &:nth-child(2) {
          margin-left: .5rem;
          margin-top:0;
        }
      }
    }
  }
}
.news {
  padding-left: 0;
  color: $primary;
  padding-bottom: 1rem;
  li {
    list-style: none;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding: .25em .75em;
    margin-bottom: 5px;
    border-left: 2px solid rgba($primary, .5);
  }
  span {
    display: block;
    font-size: 0.9em;
  }
  @include media-breakpoint-up(sm) {
    display: flex;
    li {
      padding: .5em .75em;
      margin-bottom: 0;
      border-left: 2px solid rgba($primary, .5);
      width: 25%;
    }
  }
}
$page-w: 800px;
.member {
  margin-top: 2rem;
  margin-bottom: 2rem;
  background: linear-gradient(5deg, #c2a224 0, #fdeda3 30%, #e1c96b 75%, #fdeda3 100%);
  padding: 2rem;
  border-radius: 1rem;
  >h4 {
    color: $secondary;
  }
  >p {
    color: rgba($gray-700, .7);
  }
  >.data {
    border-radius: 0.75rem;
    background-color: rgba($light, 0.6);
    padding:1.25rem 1rem;
    display: flex;
    justify-content: center;
    font-size: 2.5rem;
    i {
      color: rgba($secondary, 0.5);
      margin-right: 1rem;
    }
    p {
      color: $gray-600;
      margin-bottom: 0;
    }
  }
}
.vip {
  margin-bottom: 1rem;
  margin-top: 1.5rem;
  &-level {
    display: flex;
    margin-bottom: 0;
    >dd {
      display: flex;
      align-items: center;
      * {
        display: block;
      }
      p {
        margin: 0 0.5rem;
      }
      i {
        width: 4rem;
        height: 4rem;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
      }
      img {
        width: auto;
        max-width: 100%;
      }
      &:first-child {
        i {
          width: 3rem;
          height: 3rem;
        }
      }
      &:last-child {
        margin-left: auto;
        flex-direction: row-reverse;
        text-align: right;
      }
    }
  }
  > small {
    // color: $gray-600;
    margin-top: 0.5rem;
  }
}
.icon {
  &-v {
    background-image: url(../images/i-v.png);
  }
  &-vv {
    background-image: url(../images/i-vv.png);
  }
  &-mv {
    background-image: url(../images/i-mv.png);
  }
}
.bg-Brown {
  background-image: linear-gradient(0deg, #995b02 0%, #c5981e 85%);
}
@keyframes progress {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}
.progress {
  height: 2rem;
  background-color: rgba($black, .7);
} 
.progress-bar {
  // color: $black;
  width: 0;
  transition: width 1.5s ease;
  // animation: progress 1.5s ease forwards;
} 
.center-btn {
  text-align: center;
  margin-top: 1.25rem;
  a {
    font-size: 1.25rem;
    padding-bottom: 0.75rem;
    padding-top: 0.75rem;  
    width: 30%;
    max-width: 200px;
    border-radius: 100px;
  }
}
.nav-member {
  margin-top: 3em;
  a {
    background: linear-gradient(0deg, #494949 0%, #747474 15%, #494949 90%, #747474 100%);
    color: $primary;
    border: 1px solid rgba($primary, .5);
    border-radius: .5rem;
    text-shadow: none;
    padding: 1rem 0;
    &:hover {
      background: linear-gradient(0deg, #414040 0%, #686868 15%, #414040 90%, #686868 100%);
      text-shadow: none;
      color: $primary;
      border: 1px solid rgba($primary, .3);
    } 
    &.active {
      background: linear-gradient(0deg, #e1c96b 0%, #fdeda3 15%, #e1c96b 90%, #fdeda3 100%);
      text-shadow: none;
      &:hover {
        color: $secondary;
      }
    }
  }
  li {
    border: none;
    &:nth-child(n+2) {
      margin-left: .5rem;
    }
  }
  @include media-breakpoint-up(sm) {
    li {
      &:nth-child(n+2) {
        margin-left: 1rem;
      }
    }
  }
}

.ranking {
  background-color: #fff;
  border-radius: 1rem;
  overflow: hidden;
  margin-top: 2rem;
  margin-bottom: 2rem;
  h4 {
    text-align: center;
    line-height: 3rem;
    border-bottom: 1px solid rgba($black, .1);
    margin-bottom: 1rem;
  }
  dl {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom: 0;
    padding: 1rem;
    background: linear-gradient(5deg, #c2a224 0, #fdeda3 30%, #e1c96b 75%, #fdeda3 100%);
    h5 {
      font-weight: 700;
    }
  }
  dt {
    width: 100%;
  }
  dd {
    padding-left: 1rem;
    padding-right: 1rem;
    display: flex;
    margin-bottom: 0;
    &:nth-child(2) {
      padding-left: 0;
      padding-bottom: 1rem;
      align-items:center;
      justify-content: center;
      width: 100%;
      border-bottom: 1px solid rgba($black, .1);
      >div:last-child {
        max-width: 50%;
      }
      p {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
    &:nth-child(n+3) {
      width: 50%;
      text-align: center;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      margin-top: 1rem;
    }
    &:last-child {
      border-left: 1px solid rgba($black, .1);
    }
    @include media-breakpoint-up(sm) {
      &:nth-child(2) {
        width: 55%;
        padding-bottom: 0;
        border-bottom: none;
      }
      &:nth-child(n+3) {
        border-left: 1px solid rgba($black, .1);
        margin-top: 0;
        width: 22.5%;
      }
    }
  }
  p {
    margin-bottom: 0;
    color: rgba($black, .5);
  }
  i {
    background-repeat: no-repeat;
    background-size: auto 100%;
    background-position: center;
  }
  .icon-v, .icon-vv, .icon-mv {
    display: block;
    margin-right: .5rem;
    width: 2.5rem;
    height: 2.5rem;
  }
  h5  {
    display: flex;
  }
  >ul {
    padding: 1rem;
    list-style: none;
    li {
      display: flex;
      justify-content: space-between;
      border-top: 1px solid rgba($black, .1);
      padding: 1rem;
      >i {
        min-width: 3rem;
        width: 3rem;
        height: 3rem;
        text-align: center;
      }
      &:first-child {
        border-top: none;
        p{
          &:first-child {
            width: 3rem;
            text-align: center;
          }
        }
      }
    }
    h6 {
      display: flex;
      align-items: center;
      i {
        
        width: 1.5rem;
        height: 1.5rem;
        margin-bottom: 0;
        margin-left: 0.5rem;
      }
    }
    p {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    div {
      width: 50%;
      flex: 1 1 auto;
    }
    h5 {
      width: auto;
    }
  }
}
.icon-r {
  &-1 {
    background-image: url(../images/i-r1.png);
  }
  &-2 {
    background-image: url(../images/i-r2.png);
  }
  &-3 {
    background-image: url(../images/i-r3.png);
  }
}
.avatar-head {
  img {
    overflow: hidden;
    border-radius: 1rem;
    max-width: 8rem;
    height: auto;
  }
}
.tab-content {
  background-color: rgba($light, .6);
  margin-top: 1rem;
  margin-bottom: 2rem;
  border-radius: .5rem;
  padding: 1.5rem;
}
.tab-pane .table {
  width: 100%;
  th,td{
    border-bottom: 1px solid rgba($light, .3);
    border-top: none;
  }
  tr {
    white-space: nowrap;
  }
  td {
    &:last-child {
      text-align: right;
      > button {
        margin-left: 0.5rem;
        float: right;
      }
    }
  }
}
.table {
  thead {
    white-space: nowrap;
  }
}
.nav-page {
  text-align: center;
}
.pay {
  border-radius: .5rem;
  overflow: hidden;
  a {
    display: block;
    padding: 1rem;
    transition: all .3s;
    background-color: rgba($light, .6);
    &:hover {
      background-color: rgba($light, .8);
    }
  }
  img {
    width: auto;
    max-width: 100%;
  }
}
.chat {
  position: fixed;
  z-index: 9999;
  background-color:rgba($black, .7);
  bottom: 0;
  right: 0;
  width: calc(100% - 1rem);
  max-width: 450px;
  height: 80%;
  margin-right: 0.5rem;
  border-radius: 5px 5px 0 0;
  box-shadow: 0 0 5px $light;
  overflow: hidden;
  transform: translateY(100%);
  opacity: 0;
  transition: transform .5s ease-out, opacity .5s;
  &-title {
    background-color: lighten(rgba($secondary, .6), 10) ;
    color: $light;
    text-align: center;
    padding: .5rem 1rem;
    margin-bottom: 0;
    >a {
      float: right;
      &:hover {
        color: $light;
      }
    }
  }
  >iframe {
    height: calc(100% - 2.5rem);;
  }
  
  @include media-breakpoint-up(sm) {
    height: 50%;
  }
  @media (max-height: 575.98px) {
    height: 95%;
  }
  &.open {
    transform: translateY(0);
    opacity: 1;
  }
}
.mark {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 10;
  width: 60%;
  clip-path: polygon(0 0, 100% 0, 100% 100%, 15% 100%);
  background-image: linear-gradient(90deg, #c2a224 0, #fdeda3 25%, #e1c96b 50%, #fdeda3 75%, #e1c96b 100%);
  text-align: center;
  i {
    color: $secondary;
    font-weight: bold;
    text-transform: uppercase;
    white-space:nowrap;
    font-size: .8rem;
    line-height: 1rem;
  }
  @include media-breakpoint-up(md) {
    width: 50%;
    i {
      font-size: 1rem;
      line-height: 1.8rem;
    }
  }
}
/**
*footer區塊內容
**/
footer {
  background-color: $black;
  line-height: 1.5rem;
  color: rgba($primary, .7);
  padding-bottom: 4rem;
  @include media-breakpoint-up(md) {
    padding-bottom: 0;
  }
}

.footer-nav {
  padding-top: 1rem;
  padding-bottom: 1rem;
  display: flex;
  justify-content: center;
  border-bottom: 1px solid rgba($primary, .3);
  a {
    &:nth-child(n+2) {
      padding-left: 0.5rem;
      margin-left: 0.5rem;
      border-left: 1px solid rgba($primary, .3);
    }
  }
}
.company {
  list-style: none;
  padding-left: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-end;
  justify-content: center;
  font-size: .5rem;
  li {
    &:nth-child(n+2) {
      margin-left: .5rem;
    }
  }
  span {
    display: block;
    background-repeat: no-repeat;
    background-position: center;
    // background-size: cover;
  }
  @include media-breakpoint-up(xl) {
    margin-bottom: 0;
  }
}

.icon {
  margin-right: 10px;
  span {
    width: 3rem;
    height: 3rem;
    display: block;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
  }
  &-12 {
    background-image: url(../images/12.jpg);
  }
  &-15 {
    background-image: url(../images/15+.jpg);
  }
  &-qrcode {
    height: 3rem;
    width: 3rem;
    background-position: center center;
    background-size: auto 100%;
    background-image: url(../images/qrCode.png);
  }
  &-173yy {
    height: 3rem;
    width: 7rem;
    background-size: auto 100%;
    background-position: center center;
    background-image: url(../images/logo-s.png);
  }
  @include media-breakpoint-up(md) {
    span { 
      height: 3.75rem;
      width: 3.75rem;
    }
    &-qrcode { 
      height: 3.75rem;
      width: 3.75rem;
    }
    &-173yy { 
      height: 3.75rem;
      width: 9rem;
    }
  }
}
.grade {
  line-height: 1.25rem;
  font-size: 0.8rem;
  list-style: none;
  padding-left: 0;
  margin-bottom: 0;
}

.btn-top {
  font-size: 0.85em;
  font-family: 'Cormorant Garamond', serif;
  position: fixed;
  right: -1.5em;
  bottom: -1em;
  opacity: 0;
  transform: translateY(-100%) rotate(90deg);
  transition: all .3s;
  &.on {
    opacity: 1;
    bottom: 1em;
  }
  &:hover {
    bottom: 1.5em;
    text-decoration: none;
  }
  @include media-breakpoint-up(sm) {
    font-size: 1em;
    right: -1em;
  }
}
.modal {
  button {
    &:focus {
      outline: none;
    }
  }
  .form-control {
    padding-top: .5rem;
    padding-bottom: .5rem;
    font-size: 1.25rem;
    height: auto;
  }
  &-content {
    border-radius: 1rem;
  }
  &-header {
    // padding: 1rem;
    // align-items: center;
    .close {
      padding: 0.5rem;
      border-radius: 100px;
      background-color: $warning;
      margin: -0.5rem -0.5rem -0.5rem auto;
    }
  }
  &-title {
    text-align: center;
  }
  &-icon {
    display: inline-block;
    width: 5rem;
    height: 5rem;
    padding: .5rem;
    border: 2px solid $danger;
    border-radius: 100px;
    margin-top: 2rem;
    margin-bottom: 2rem;
    i {
      font-size: 2.5rem;
      color: $danger;
    }
  }
}
.step {
  padding-left: 0;
  list-style: none;
  display: flex;
  justify-content: center;
  li {
    width: 2.5rem;
    height: 2.5rem;
    background-color:$yellow;
    border-radius: 100px;
    text-align: center;
    line-height: 2.5rem;
    opacity: .6;
    &:nth-child(2) {
      margin-left: 2rem;
      position: relative;
      &::before {
        position: absolute;
        content: "";
        margin: auto;
        top: 0;
        bottom: 0;
        left: -2rem;
        height: 5px;
        width: 2rem;
        background-color: $yellow;
      }
    }
    &.on {
      opacity: 1;
    }
  }
}
.form-btn {
  padding-top: 2rem;
  text-align: center;
  button {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    &:nth-child(n+2) {
      margin-left: 1rem;
    }
  }
}

.border-warning.border-warning{
  &:focus {
    box-shadow: 0 0 3px rgba($warning, .7);
  }
}
