.play {
  display: flex;
  height: 100%;
  width: 100%;
  top: 0;
  bottom:0;
  overflow: hidden;
  position: absolute;
  &-game {
    position: relative;
    display: flex;
    justify-content: flex-end;
    // align-items: center;
    flex-grow: 2;
    width: 70%;
    transition: all .5s ease;
    &.active {
      width: 100%;
    }
    .game {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: 0;
    }
    // @include media-breakpoint-up(md) {
    //   align-items: flex-start;
    // }
  }
  &-aside {
    display: none;
    section {
      height: 100%;
      overflow-x: hidden;
      overflow-y: auto;
      min-width: calc(350px - 1rem);
      padding-left: 0.5rem;
      padding-right: 0.5rem;
      padding-bottom: 1rem;
    }
    flex-grow: 1;
    width: 0;
    min-width: 0;
    background-color: #1d1d1d;
    box-shadow: 0 0 10px rgba($black, .7);
    transition: all .5s ease;
    position: relative;
    &.active {
      width: 30%;
      max-width: 450px;
      min-width: 350px;
    }
    &_logo {
      background: linear-gradient(0deg, #0E0E0E 10%, #363636 100%);
      text-align: center;
      margin-left: -0.5rem;
      margin-right: -0.5rem;
      padding-top: 0.5rem;
      padding-bottom: 0.5rem;
      img {
        max-width: 5rem;
      }
    }
    &_member {
      background:linear-gradient(20deg, #c2a224 0, #fdeda3 30%, #e1c96b 75%, #fdeda3 100%);
      padding: 1rem;
      border-radius: .5rem;
      margin-top: 0.5rem;
      margin-bottom: 0.5rem;
      p {
        color: $secondary;
        display: flex;
        align-items: center;
        font-size: 1.15rem;
        img {
          width: 4rem;
          height: 4rem;
          border-radius: 200px;
        }
        &.user {
          padding-top: 0.5rem;
          padding-bottom: 0.5rem;
          margin-bottom: 0;
          border-top: 1px solid rgba($secondary, 0.5);
        }
      }
    }
    &_nav {
      display: flex;
      justify-content: center;
      a {
        color: $secondary;
        font-size: 1.2rem;
        line-height: 2.5rem;
        border: 1px solid lighten($warning, 15);
        border-radius: 200px;
        width: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        &:nth-child(n+2) {
          margin-left: .5rem;
        }
      }
      i {
        font-size: 2rem;
        margin-right: 1rem;
      }
    }
    @include media-breakpoint-up(lg) {
      display: block;
    }
  }
}
.game-list {
  display: flex;
  flex-wrap: wrap;

  // justify-content: space-between;
  &.multi {
    div {
      width: calc(50% - 0.25rem);
      margin-left: 0.5rem;
      &:first-child {
        margin-left: 0;
      }
    }
    nav {
      a {
        // width: 50%;
        &:last-child {
          margin-left: .25rem;
        }
      }
    }
  }
  &.single {
    div {
      position: relative;
      overflow: hidden;
      width: calc(100% / 3 - 0.35rem );
      margin-left: 0.5rem;
      border-radius: .5rem;
      &:nth-child(3n+1) {
        margin-left: 0;
      }
      &:nth-child(n+4) {
        margin-top: 1rem;
      }
    }
    span.mark {
      width: 60%;
      i {
        font-size: 0.9em;
        line-height: .9em;
      }
    }
    nav {
      flex-wrap: wrap;
      a {
        width: 95%;
        &:last-child {
          margin-top: .25rem;
        }
      }
    }
  }
  img {
    border: 1px solid rgba($warning,.5);
    border-radius: .5rem;
    max-width: 100%;
  }
  p {
    color: $primary;
    font-size: 1.1rem;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  nav {
    display: flex;
    justify-content: center;
    a {
      line-height: 1.25rem;
      border: 1px solid lighten($warning, 15);
      color: $secondary;
      
    }
  }
}
.btn-open {
  color: $secondary;
  background: linear-gradient(0deg, #e1c96b 0%, #fdeda3 15%, #e1c96b 90%, #fdeda3 100%);
  border-top-right-radius: 0 ;
  border-bottom-right-radius: 0 ;
  position: absolute;
  margin: auto;
  top: 0;
  bottom: 0;
  width: 3rem;
  height: 3.25rem;
  transform: translateX(-100%);
  i {
    display: block;
    transform:scaleX(-1);
  }
  &:hover {
    color: $secondary;
    filter: brightness(1.1);
  }
  &.active {
    i{
      transform:scaleX(1);
    }
  }
}
.touchBar {
  position: relative;
  z-index: 100;
  border-radius: 100px;
  display: flex;
  flex-wrap: wrap;
  width: 4rem;
  height: 4rem;
  margin-top: 2.5rem;
  margin-right: 10px;
  background:$golden;
  text-align: center;
  cursor: pointer;
  overflow: hidden;
  transition: height .3s, opacity .3s ease;
  .handle {
    width:4rem;
    height:4rem;
    background: url(../images/logo-t.png) center 1.25rem no-repeat;
    background-size: 80% auto;
  }
  &.is-pointer-down {
    opacity: .75;
  }
  &.on {
    height: 24rem;
  }
  a {
    color: $secondary;
    border-radius: 100px;
    width: 4rem;
    height: 4rem;
    line-height: 4rem;
    font-size: 1.7rem;
    transition: all .3s;
    z-index: 1;
    &:hover {
      background-color: rgba($light, .5);
      text-decoration: none;
    }
  }
  @media (orientation: landscape) {
    flex-direction: row-reverse;
    transition: width .3s, opacity .3s ease;
    &.on {
      height: 4rem;
      width: 24rem;
      a {
        display: block;
        opacity: 1;
      }
    }
    a {
      display: none;
      opacity: 0;
    }
  }
  // @include media-breakpoint-up(md) {
  //   margin-top: 10px;
  // }
  @include media-breakpoint-up(lg) {
    display: none;
  }
}